.container {
  display: flex;
  width: 100vw;
  height: 100vh;
  justify-content: center;
  align-items: center;
  background-color: rgb(27, 24, 24);
}

.component {
  max-width: 500px;
  width: 100%;
  border: 1px dashed rgb(184, 181, 181);
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: rgb(250, 241, 241);
  border-radius: 3px;
  box-shadow: -4px 5px 9px 0px rgb(108 85 85);
  padding: 10px;
}
.component > h1 {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 70px;
  color: rgb(147, 128, 128);
  padding: 25px 10px 35px;
  text-align: center;
}

.component > form {
  width: 100%;
  padding: 0 10px;
  display: flex;
  justify-content: space-evenly;
}
.component > form > .add-input {
  height: 30px;
  width: 70%;
  outline: 1px solid rgb(108 85 85);
  border-radius: 5px;
  padding: 10px;
  color: rgb(108 85 85);
}
.component > form > .add-input::placeholder {
  color: rgb(205, 180, 180);
}
.component > form > button {
  height: 55px;
  width: 20%;
  background-color: rgb(108 85 85);
  border: 1px solid rgb(108 85 85);
  border-radius: 5px;
  color: white;
  padding: 10px;
  cursor: pointer;
  font-size: 20px;
}
.list {
  width: 100%;
}
.todo-items {
  width: 90%;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: rgb(108 85 85);
}
.btns {
  display: flex;
  gap: 5px;
}
.btns > button,
.cancelbtn {
  background-color: rgb(108 85 85);
  border: 1px solid rgb(108 85 85);
  border-radius: 5px;
  color: white;
  padding: 10px;
}
